/* ===== QUILL EDITOR STYLING WITH SNOW THEME RESTORATION ===== */

/* Editor Container */
.quill-editor-container {
  isolation: isolate;
  background: #fff;
  border: 1px solid #CED4DA;
  border-radius: 0.5rem;
  overflow: hidden;
  width: 100%;
  max-width: 100%;
  min-width: 0;
  flex: 1;
  display: flex;
  flex-direction: column;
}

.quill-editor-container.is-invalid {
  border-color: #dc3545;
}

/* Restore Snow theme look */
.quill-editor-container .ql-toolbar.ql-snow {
  display: flex !important;
  border: 0;
  border-bottom: 1px solid #E5E7EB;
  background: #F8F9FA;
  padding: 8px;
  width: 100%;
  flex-shrink: 0;
}

.quill-editor-container .ql-container.ql-snow {
  display: flex !important;
  border: 0;
  height: 340px;
  background: #fff;
  width: 100%;
  flex: 1;
  flex-direction: column;
}

.quill-editor-container .ql-editor {
  min-height: 300px;
  padding: 14px 16px;
  overflow-y: auto;
  direction: rtl;
  text-align: right;
  font-family: 'IBM Plex Sans Arabic', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  font-size: 14px;
  line-height: 1.6;
  background: #fff;
  border: none;
  width: 100%;
  max-width: 100%;
  box-sizing: border-box;
  flex: 1;
}

/* RTL/LTR text direction support */
.quill-editor-container .ql-editor[dir="rtl"] {
  direction: rtl;
  text-align: right;
}

.quill-editor-container .ql-editor[dir="ltr"] {
  direction: ltr;
  text-align: left;
}

/* Toolbar buttons */
.quill-editor-container .ql-toolbar button {
  border: none !important;
  background: transparent !important;
  cursor: pointer !important;
  padding: 5px !important;
  border-radius: 3px !important;
  display: inline-block !important;
  visibility: visible !important;
  opacity: 1 !important;
  margin: 0 2px !important;
  min-width: 28px !important;
  height: 28px !important;
  line-height: 1 !important;
  transition: all 0.2s ease !important;
}

.quill-editor-container .ql-toolbar button:hover {
  background-color: #e9ecef !important;
  transform: scale(1.05) !important;
}

.quill-editor-container .ql-toolbar button.ql-active {
  background-color: #007bff !important;
  color: white !important;
  box-shadow: 0 2px 4px rgba(0, 123, 255, 0.3) !important;
}

/* Ensure formatting buttons work on selected text */
.quill-editor-container .ql-toolbar button.ql-bold.ql-active,
.quill-editor-container .ql-toolbar button.ql-italic.ql-active,
.quill-editor-container .ql-toolbar button.ql-underline.ql-active,
.quill-editor-container .ql-toolbar button.ql-strike.ql-active {
  background-color: #007bff !important;
  color: white !important;
}

/* Formatting button icons */
.quill-editor-container .ql-toolbar .ql-bold::before {
  content: 'B' !important;
  font-weight: bold !important;
}

.quill-editor-container .ql-toolbar .ql-italic::before {
  content: 'I' !important;
  font-style: italic !important;
}

.quill-editor-container .ql-toolbar .ql-underline::before {
  content: 'U' !important;
  text-decoration: underline !important;
}

.quill-editor-container .ql-toolbar .ql-strike::before {
  content: 'S' !important;
  text-decoration: line-through !important;
}

.quill-editor-container .ql-toolbar .ql-formats {
  margin-right: 15px !important;
  display: inline-block !important;
  vertical-align: middle !important;
}

.quill-editor-container .ql-toolbar .ql-picker {
  display: inline-block !important;
  visibility: visible !important;
  opacity: 1 !important;
  margin: 0 2px !important;
}

.quill-editor-container .ql-toolbar .ql-picker-label {
  border: 1px solid transparent !important;
  padding: 5px 8px !important;
  border-radius: 3px !important;
  cursor: pointer !important;
  display: inline-block !important;
  min-width: 60px !important;
  height: 28px !important;
  line-height: 1.2 !important;
  font-size: 12px !important;
}

.quill-editor-container .ql-toolbar .ql-picker-label:hover {
  background-color: #e9ecef !important;
}

/* Better Table styling */
.quill-editor-container .ql-editor table {
  border-collapse: collapse;
  margin: 10px 0;
  width: 100%;
  max-width: 100%;
  table-layout: fixed;
  direction: rtl;
}

.quill-editor-container .ql-editor table td,
.quill-editor-container .ql-editor table th {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: right;
  vertical-align: top;
  min-width: 50px;
  word-wrap: break-word;
  position: relative;
}

.quill-editor-container .ql-editor table th {
  background-color: #f5f5f5;
  font-weight: bold;
}

/* Better table cell selection and editing */
.quill-editor-container .ql-editor table td:focus,
.quill-editor-container .ql-editor table th:focus {
  outline: 2px solid #007bff;
  outline-offset: -2px;
}

/* Better table toolbar */
.quill-editor-container .ql-better-table {
  position: relative;
}

.quill-editor-container .ql-better-table .qlbt-operation-menu {
  position: absolute;
  background: white;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  padding: 5px;
  z-index: 1000;
  min-width: 200px;
}

.quill-editor-container .ql-better-table .qlbt-operation-menu button {
  margin: 2px;
  padding: 6px 12px;
  font-size: 12px;
  border: 1px solid #ddd;
  background: white;
  cursor: pointer;
  border-radius: 3px;
  display: block;
  width: 100%;
  text-align: right;
}

.quill-editor-container .ql-better-table .qlbt-operation-menu button:hover {
  background: #f0f0f0;
}

/* Table cell operations */
.quill-editor-container .ql-better-table .qlbt-cell-operations {
  display: flex;
  flex-wrap: wrap;
  gap: 2px;
  margin-bottom: 5px;
}

.quill-editor-container .ql-better-table .qlbt-cell-operations button {
  padding: 4px 8px;
  font-size: 11px;
  border: 1px solid #ddd;
  background: #f8f9fa;
  cursor: pointer;
  border-radius: 3px;
  flex: 1;
  min-width: 60px;
}

.quill-editor-container .ql-better-table .qlbt-cell-operations button:hover {
  background: #e9ecef;
}

/* Table row/column operations */
.quill-editor-container .ql-better-table .qlbt-row-operations,
.quill-editor-container .ql-better-table .qlbt-column-operations {
  margin: 5px 0;
  padding: 5px;
  border-top: 1px solid #eee;
}

.quill-editor-container .ql-better-table .qlbt-row-operations button,
.quill-editor-container .ql-better-table .qlbt-column-operations button {
  margin: 2px;
  padding: 4px 8px;
  font-size: 11px;
  border: 1px solid #ddd;
  background: white;
  cursor: pointer;
  border-radius: 3px;
  display: inline-block;
}

.quill-editor-container .ql-better-table .qlbt-row-operations button:hover,
.quill-editor-container .ql-better-table .qlbt-column-operations button:hover {
  background: #f0f0f0;
}

/* Table selection styling */
.quill-editor-container .ql-better-table .qlbt-selected-cell {
  background-color: rgba(0, 123, 255, 0.1) !important;
  border: 2px solid #007bff !important;
}

.quill-editor-container .ql-better-table .qlbt-selected-row {
  background-color: rgba(0, 123, 255, 0.05) !important;
}

.quill-editor-container .ql-better-table .qlbt-selected-column {
  background-color: rgba(0, 123, 255, 0.05) !important;
}

/* Page break styling */
.quill-editor-container .ql-editor .ql-pagebreak {
  display: block;
  margin: 20px 0;
  text-align: center;
  border: none;
  border-top: 2px dashed #ccc;
  color: #666;
  font-size: 12px;
}

.quill-editor-container .ql-editor .ql-pagebreak::before {
  content: "--- Page Break ---";
  background: white;
  padding: 0 10px;
}

/* Special characters styling */
.quill-editor-container .ql-special-char {
  font-family: 'Times New Roman', serif;
  font-size: 16px;
}

/* Special characters button styling */
.quill-editor-container .ql-toolbar button[data-value="special-char"] {
  position: relative;
}

.quill-editor-container .ql-toolbar button[data-value="special-char"]::before {
  content: "Ω";
  font-size: 14px;
  font-weight: bold;
}

/* Font size controls */
.quill-editor-container .ql-size-small {
  font-size: 0.75em;
}

.quill-editor-container .ql-size-large {
  font-size: 1.5em;
}

.quill-editor-container .ql-size-huge {
  font-size: 2.5em;
}

/* RTL alignment support */
.quill-editor-container .ql-editor .ql-align-right {
  text-align: right;
}

.quill-editor-container .ql-editor .ql-align-center {
  text-align: center;
}

.quill-editor-container .ql-editor .ql-align-left {
  text-align: left;
}

.quill-editor-container .ql-editor .ql-align-justify {
  text-align: justify;
}

/* Enhanced toolbar layout for better organization */
.quill-editor-container .ql-toolbar {
  flex-wrap: wrap !important;
  gap: 5px !important;
  padding: 10px !important;
}

/* Color picker styling */
.quill-editor-container .ql-color-picker,
.quill-editor-container .ql-background-picker {
  width: 28px !important;
  height: 28px !important;
  padding: 0 !important;
  border: 1px solid #ccc !important;
  border-radius: 3px !important;
}

/* Dropdown styling */
.quill-editor-container .ql-picker-options {
  background: white !important;
  border: 1px solid #ccc !important;
  border-radius: 4px !important;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1) !important;
  z-index: 1000 !important;
}

.quill-editor-container .ql-picker-item {
  padding: 5px 10px !important;
  cursor: pointer !important;
}

.quill-editor-container .ql-picker-item:hover {
  background-color: #f0f0f0 !important;
}

/* Lists: show normal bullets/numbers, kill custom pseudo elements */
.quill-editor-container .ql-editor ul { 
  list-style: disc; 
  padding-inline-start: 1.25rem; 
  margin: 0 0 1rem 0;
}

.quill-editor-container .ql-editor ol { 
  list-style: decimal; 
  padding-inline-start: 1.25rem; 
  margin: 0 0 1rem 0;
}

.quill-editor-container .ql-editor li { 
  position: static; 
  list-style: inherit; 
  margin: 0;
  padding: 0;
}

/* Kill custom pseudo elements that cause artifacts */
.quill-editor-container .ql-editor li::before,
.quill-editor-container .ql-editor li::after,
.quill-editor-container .ql-editor *::before,
.quill-editor-container .ql-editor *::after {
  content: none !important;
  border: 0 !important;
  background: none !important;
  display: none !important;
  visibility: hidden !important;
  opacity: 0 !important;
  width: 0 !important;
  height: 0 !important;
  margin: 0 !important;
  padding: 0 !important;
  position: static !important;
  float: none !important;
  clear: none !important;
}

/* Paragraph spacing */
.quill-editor-container .ql-editor p { 
  margin: 0 0 0.5rem; 
}

/* Headers */
.quill-editor-container .ql-editor h1,
.quill-editor-container .ql-editor h2,
.quill-editor-container .ql-editor h3,
.quill-editor-container .ql-editor h4,
.quill-editor-container .ql-editor h5,
.quill-editor-container .ql-editor h6 {
  margin: 0 0 0.5rem 0;
  font-weight: 600;
  line-height: 1.2;
}

.quill-editor-container .ql-editor h1 { font-size: 2rem; }
.quill-editor-container .ql-editor h2 { font-size: 1.75rem; }
.quill-editor-container .ql-editor h3 { font-size: 1.5rem; }
.quill-editor-container .ql-editor h4 { font-size: 1.25rem; }
.quill-editor-container .ql-editor h5 { font-size: 1rem; }
.quill-editor-container .ql-editor h6 { font-size: 0.875rem; }

/* Focus states */
.quill-editor-container:focus-within {
  border-color: #86b7fe;
  outline: 0;
  box-shadow: 0 0 0 0.25rem rgba(13, 110, 253, 0.25);
}

/* ===== CONTENT DISPLAY STYLES ===== */
.letter-content-display {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  font-size: 14px;
  line-height: 1.6;
  color: #343a40;
}

.letter-content-display h1,
.letter-content-display h2,
.letter-content-display h3,
.letter-content-display h4,
.letter-content-display h5,
.letter-content-display h6 {
  margin-top: 1.5rem;
  margin-bottom: 0.75rem;
  font-weight: 600;
  line-height: 1.2;
}

.letter-content-display h1 {
  font-size: 2rem;
}

.letter-content-display h2 {
  font-size: 1.75rem;
}

.letter-content-display h3 {
  font-size: 1.5rem;
}

.letter-content-display h4 {
  font-size: 1.25rem;
}

.letter-content-display h5 {
  font-size: 1rem;
}

.letter-content-display h6 {
  font-size: 0.875rem;
}

.letter-content-display p {
  margin-bottom: 1rem;
}

.letter-content-display ul,
.letter-content-display ol {
  margin-bottom: 1rem;
  padding-left: 20px;
}

.letter-content-display li {
  margin-bottom: 0.5rem;
}

.letter-content-display strong {
  font-weight: bold;
}

.letter-content-display em {
  font-style: italic;
}

.letter-content-display u {
  text-decoration: underline;
}

.letter-content-display a {
  color: #007bff;
  text-decoration: none;
}

.letter-content-display a:hover {
  text-decoration: underline;
}

.letter-content-display blockquote {
  border-left: 4px solid #ced4da;
  padding-left: 15px;
  margin-left: 0;
  font-style: italic;
  color: #6c757d;
}

.letter-content-display table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 1rem;
  background-color: #f8f9fa;
  border: 1px solid #dee2e6;
}

.letter-content-display th,
.letter-content-display td {
  border: 1px solid #dee2e6;
  padding: 0.75rem;
  text-align: left;
  vertical-align: top;
}

.letter-content-display th {
  background-color: #e9ecef;
  font-weight: bold;
  color: #495057;
}

.letter-content-display .ql-align-left {
  text-align: left;
}

.letter-content-display .ql-align-center {
  text-align: center;
}

.letter-content-display .ql-align-right {
  text-align: right;
}

.letter-content-display .ql-align-justify {
  text-align: justify;
}

.letter-content-display .ql-size-small {
  font-size: 0.75em;
}

.letter-content-display .ql-size-large {
  font-size: 1.25em;
}

.letter-content-display .ql-size-huge {
  font-size: 1.5em;
}

.letter-content-display .ql-indent-1 {
  padding-left: 3em;
}

.letter-content-display .ql-indent-2 {
  padding-left: 6em;
}

.letter-content-display .ql-indent-3 {
  padding-left: 9em;
}

.letter-content-display .ql-indent-4 {
  padding-left: 12em;
}

.letter-content-display .ql-indent-5 {
  padding-left: 15em;
}

.letter-content-display .ql-indent-6 {
  padding-left: 18em;
}

.letter-content-display .ql-indent-7 {
  padding-left: 21em;
}

.letter-content-display .ql-indent-8 {
  padding-left: 24em;
}

/* Responsive Design */
@media (max-width: 768px) {
  .quill-editor-container .ql-editor {
    min-height: 250px;
    max-height: 400px;
  }

  .quill-editor-container .ql-toolbar {
    padding: 4px;
  }

  .quill-editor-container .ql-toolbar .ql-formats {
    margin-right: 8px;
  }

  .quill-editor-container .ql-toolbar button {
    padding: 3px;
    font-size: 12px;
  }

  .letter-content-display h1 {
    font-size: 1.5rem;
  }

  .letter-content-display h2 {
    font-size: 1.25rem;
  }

  .letter-content-display table {
    font-size: 12px;
  }

  .letter-content-display th,
  .letter-content-display td {
    padding: 0.5rem;
  }
}

/* RTL Support */
[dir="rtl"] .letter-content-display {
  text-align: right;
}